import React, {useEffect, useState} from 'react';
import './IndexPage.css';
import {Typography} from "antd";

const {Title, Paragraph} = Typography;

const IndexPage = () => {
  const [ipInfo, setIpInfo] = useState(null);

  useEffect(() => {
    fetchIpInfo().then(() => console.log("end fetch"));
  }, []);

  const fetchIpInfo = async () => {
    try {
      const unixTime = Date.now();
      const url = `https://pubstatic.b0.upaiyun.com/?_upnode&t=${unixTime}`;
      const response = await fetch(url);
      const data = await response.json();
      setIpInfo(data);
    } catch (error) {
      console.error('Error fetching IP info:', error);
    }
  };

  return (
    <div className="index-page">
      <Title level={2}></Title>
      {ipInfo && (
        <div>
          <Paragraph>
            <strong>IP:</strong> {ipInfo.remote_addr}
          </Paragraph>
          <Paragraph>
            <strong>:</strong> {ipInfo.remote_addr_location.province}, {ipInfo.remote_addr_location.city}
          </Paragraph>
          <Paragraph>
            <strong>:</strong> {new Date().toLocaleString()}
          </Paragraph>
        </div>
      )}
    </div>
  );
};

export default IndexPage;
