// video-hub-front/src/components/videohub/VideoHub.js
import React, {useState} from 'react';
import {
  HomeTwoTone, BookTwoTone, VideoCameraTwoTone, InfoCircleTwoTone
} from '@ant-design/icons';
import './VideoHub.css';
import IndexPage from '../index/IndexPage'; //  IndexPage 
import RedBookPage from '../redbook/RedBookPage'; //  RedBookPage 
import TikTokPage from '../tiktok/TikTokPage'; //  TikTokPage 
import AboutPage from '../about/AboutPage'; //  AboutPage 
import {Layout, Menu} from 'antd'; //  Layout 

const {Footer} = Layout; //  Layout  Footer

const VideoHub = () => {
  const [selectedMenu, setSelectedMenu] = useState('index');

  const renderPage = () => {
    switch (selectedMenu) {
      case 'index':
        return <IndexPage/>;
      case 'redbook':
        return <RedBookPage/>;
      case 'tiktok':
        return <TikTokPage/>;
      case 'about':
        return <AboutPage/>;
      default:
        return null;
    }
  };

  return (
    <Layout className="video-hub-container">
      <div>
        <Menu
          theme="light"
          mode="horizontal"
          selectedKeys={[selectedMenu]}
          onClick={({ key }) => setSelectedMenu(key)}
        >
          <Menu.Item key="index" icon={<HomeTwoTone />}>
            
          </Menu.Item>
          <Menu.Item key="redbook" icon={<BookTwoTone />}>
            
          </Menu.Item>
          <Menu.Item key="tiktok" icon={<VideoCameraTwoTone />}>
            TikTok
          </Menu.Item>
          <Menu.Item key="about" icon={<InfoCircleTwoTone />}>
            
          </Menu.Item>
        </Menu>
      </div>
      <Layout className="content-layout">
        <div className="content">
          {renderPage()}
        </div>
        <Footer className="ant-layout-footer">Video-Hub-Org 2023 Created by @fmz200</Footer>
      </Layout>
    </Layout>
  );
};

export default VideoHub;
